.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #eacf4f;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.seccion {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .seccion {
    padding: 8rem 0;
  }
}

.heading {
  text-align: center;
  margin-bottom: 3rem;
}
.heading h3 {
  margin: 1rem 0;
  font-size: 3rem;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 500;
}
.heading img {
  margin: 0 auto;
}

.boton {
  display: inline-block;
  margin-top: 1rem;
  cursor: pointer;
  color: #FFFFFF;
  border: 0.2rem solid rgba(255, 255, 255, 0.3);
  background: no-repeat;
  font-size: 1.7rem;
  padding: 1rem 3rem;
  transition: 0.2s linear;
}
.boton:hover {
  color: #000;
  background: #FFFFFF;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
  scroll-padding-top: 8rem;
  overflow-x: hidden;
}
html::-webkit-scrollbar {
  width: 1rem;
}
html::-webkit-scrollbar-track {
  background: #000;
}
html::-webkit-scrollbar-thumb {
  background: #eacf4f;
  border-radius: 5rem;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
  background-image: url(../img/background.jpg);
  background-attachment: fixed;
  background-position: center;
}

p {
  font-size: 2rem;
  line-height: 2;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

section {
  padding: 3rem 9%;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

@media (max-width: 480px) {
  html {
    font-size: 50%;
  }
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  background-color: #000;
  padding: 1.5rem 3rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 1rem;
}
@media (min-width: 1024px) {
  .header {
    justify-content: space-between;
    gap: 2rem;
    padding: 3rem;
  }
}
.header .icons {
  color: #FFFFFF;
  cursor: pointer;
  font-size: 2.5rem;
}
.header .icons:hover {
  color: #eacf4f;
}
.header .icons .fa-times {
  transform: rotate(180deg);
}
.header #menu-btn {
  display: inline-block;
}
@media (min-width: 1024px) {
  .header #menu-btn {
    display: none;
  }
}
.header .navegacion {
  position: absolute;
  background-color: #000;
  top: 99%;
  left: 0;
  right: 0;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  transition: clip-path 0.2s linear;
}
@media (min-width: 1024px) {
  .header .navegacion {
    position: relative;
    display: flex;
    justify-content: space-evenly;
    clip-path: none;
    gap: 6rem;
  }
}
.header .navegacion.active {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}
.header .navegacion a {
  color: #FFFFFF;
  font-size: 2rem;
  margin: 2rem;
  display: block;
  transition: all 0.2s linear;
}
.header .navegacion a:hover {
  color: #eacf4f;
  text-decoration: underline;
  text-underline-offset: 1rem;
}
@media (min-width: 1024px) {
  .header .navegacion a {
    margin: 0;
  }
}
.header .navegacion .espacio {
  display: block;
}
@media (min-width: 1024px) {
  .header .navegacion .espacio {
    display: inline-block;
    width: 16rem;
  }
}
.header .logo {
  position: absolute;
  transform: translateX(-80%);
  left: 37%;
}
@media (min-width: 768px) {
  .header .logo {
    transform: translateX(-170%);
  }
}
@media (min-width: 1024px) {
  .header .logo {
    transform: translateX(37%);
  }
}
.header .logo img {
  width: 15rem;
}
.header .search-form {
  height: 7rem;
  padding: 0 2rem;
  border-top: 0.1rem solid #aaa;
  display: flex;
  align-items: center;
  background-color: #000;
  position: absolute;
  top: 99%;
  left: 0;
  right: 0;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  transition: clip-path 0.2s;
}
.header .search-form.active {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}
.header .search-form input {
  width: 100%;
  background: none;
  text-transform: none;
  color: #FFFFFF;
  font-size: 1.6rem;
  border-style: none;
}
.header .search-form input:focus-visible {
  outline: none;
}

.submenu {
  position: relative;
  cursor: pointer;
}
.submenu #carrito {
  display: none;
}
.submenu #carrito #vaciar-carrito {
  background-color: red;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border: 2px solid red;
  border-radius: 12px;
  font-size: 1.5rem;
  margin-top: 2rem;
}
.submenu #carrito #vaciar-carrito:hover {
  cursor: pointer;
  background-color: #cc0000;
}
.submenu #carrito #realizar-pedido {
  background-color: #eacf4f;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border: 2px solid #eacf4f;
  border-radius: 12px;
  font-size: 1.5rem;
  margin-top: 1rem;
}
.submenu #carrito #realizar-pedido:hover {
  cursor: pointer;
  background-color: #e5c321;
}
.submenu #carrito table {
  color: #FFFFFF;
}
.submenu #carrito table th {
  font-weight: 500;
}
.submenu:hover #carrito {
  display: block;
  position: absolute;
  right: 0;
  top: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.9294117647);
  padding: 20px 10px;
  min-height: 200px;
  min-width: 300px;
}
.submenu .borrar-hamburguesa {
  background-color: rgb(192, 15, 15);
  color: #FFFFFF;
  border-radius: 40%;
  padding: 3px;
}
.submenu .borrar-hamburguesa:hover {
  color: #000;
}
.submenu .precio-total {
  color: #FFFFFF;
}

.inicio {
  margin-top: 7rem;
  background-image: url(../img/home-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.inicio .contenido {
  max-width: 70rem;
  margin: 0 auto;
  text-align: center;
}
.inicio .contenido h3 {
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 4rem;
  font-weight: 500;
  margin: 0;
}
.inicio .contenido p {
  margin: 0;
  padding: 2rem 0;
  color: #aaa;
  font-size: 1.7rem;
}
.inicio .contenido .btn {
  display: inline-block;
  margin-top: 1rem;
  cursor: pointer;
  color: #FFFFFF;
  border: 0.2rem solid rgba(255, 255, 255, 0.3);
  background: no-repeat;
  font-size: 1.7rem;
  padding: 1rem 3rem;
  transition: all 0.2s;
}
.inicio .contenido .btn:hover {
  color: #000;
  background: #FFFFFF;
}

.servicios {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
  text-align: center;
  padding: 2rem;
}
@media (min-width: 768px) {
  .servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.servicios .servicio {
  margin-bottom: 1.5rem;
}
.servicios .servicio:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .servicios .servicio {
    margin-bottom: 0;
  }
}
.servicios .servicio i {
  height: 7rem;
  width: 7rem;
  font-size: 2.5rem;
  line-height: 7rem;
  color: #000;
  background-color: #eacf4f;
  border-radius: 50%;
  margin-bottom: 1rem;
}
.servicios .servicio h3 {
  font-size: 2rem;
  padding: 1rem 0;
  text-transform: uppercase;
  margin: 0;
  color: #FFFFFF;
  font-weight: 500;
}
.servicios .servicio p {
  font-size: 1.5rem;
  color: #aaa;
  margin: 0;
}

.menu .menu__grid {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .menu .menu__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.hamburguesa {
  text-align: center;
}
.hamburguesa img {
  width: 25rem;
  margin: 0 auto;
}
.hamburguesa:hover img {
  transition: all 0.2s linear;
  transform: translateY(1rem);
}
.hamburguesa .contenido {
  padding: 1rem 0;
}
.hamburguesa .contenido h3 {
  font-size: 2rem;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 0;
}
.hamburguesa .contenido .precio {
  font-size: 2.5rem;
  line-height: 2;
  color: #aaa;
  margin: 0;
}
.hamburguesa .contenido .estrellas {
  margin-bottom: 2rem;
}
.hamburguesa .contenido .estrellas i {
  color: #eacf4f;
  font-size: 1.7rem;
}

.informacion {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 3rem;
}
.informacion__imagen {
  flex: 1 1 40rem;
}
.informacion__imagen img {
  width: 100%;
}
.informacion__contenido {
  flex: 1 1 40rem;
}
.informacion__contenido .titulo {
  font-size: 2rem;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 0;
}
.informacion__contenido p {
  padding: 2rem 0;
  font-size: 1.5rem;
  line-height: 2;
  color: #aaa;
  margin: 0;
}
.informacion__contenido .iconos {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin: 1rem 0 2rem 0;
}
.informacion__contenido .iconos h3 {
  flex: 1 1 16rem;
  font-size: 1.6rem;
  line-height: 2;
  color: #aaa;
  margin: 0;
}
.informacion__contenido .iconos h3 i {
  padding-right: 0.5rem;
  color: #eacf4f;
}

.resenas__grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .resenas__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.resenas .resena {
  text-align: center;
  padding: 2rem;
}
.resenas .resena img {
  height: 10rem;
  width: 10rem;
  border-radius: 50%;
  margin: 0 auto;
}
.resenas .resena h3 {
  font-size: 2rem;
  color: #FFFFFF;
  margin: 1rem 0;
  text-transform: uppercase;
}
.resenas .resena p {
  font-size: 1.4rem;
  line-height: 2;
  color: #aaa;
  margin: 0;
}
.resenas .resena .estrellas {
  padding-top: 1rem;
}
.resenas .resena .estrellas i {
  color: #eacf4f;
  font-size: 1.7rem;
}

.contacto__flex {
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
}
.contacto__flex .mapa {
  width: 100%;
  flex: 1 1 40rem;
}

.formulario {
  flex: 1 1 40rem;
}
.formulario .iconos {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  margin-bottom: 3rem;
}
.formulario .iconos .icono {
  text-align: center;
  flex: 1 1 17rem;
}
.formulario .iconos .icono i {
  height: 6rem;
  width: 6rem;
  line-height: 6rem;
  border-radius: 50%;
  background-color: #eacf4f;
  color: #000;
  font-size: 2rem;
  margin-bottom: 2rem;
}
.formulario .iconos .icono h3 {
  font-size: 2rem;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 0;
}
.formulario .iconos .icono p {
  font-size: 1.5rem;
  line-height: 2;
  color: #aaa;
  margin: 0;
}
.formulario form {
  text-align: center;
}
.formulario form .box {
  padding: 1.2rem 1.4rem;
  font-size: 1.6rem;
  color: #FFFFFF;
  background: none;
  border: 0.2rem solid rgba(255, 255, 255, 0.3);
  text-transform: none;
  width: 100%;
  margin: 0.7rem 0;
}
.formulario form .box:focus {
  border-color: #eacf4f;
}
.formulario form .box textarea {
  height: 15rem;
  resize: none;
}
.formulario form .boton {
  display: inline-block;
  margin-top: 1rem;
  cursor: pointer;
  color: #FFFFFF;
  border: 0.2rem solid rgba(255, 255, 255, 0.3);
  background: no-repeat;
  font-size: 1.7rem;
  padding: 1rem 3rem;
}
.formulario form .boton:hover {
  color: #000;
  background: #FFFFFF;
}

.blogs__grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .blogs__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.blog {
  position: relative;
}
.blog:hover .imagen img {
  transform: scale(1.1);
}
.blog:hover .imagen .iconos {
  transform: translateY(0);
}
.blog .imagen {
  height: 25rem;
  overflow: hidden;
  position: relative;
}
.blog .imagen .iconos {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem;
  background-color: #000;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  transform: translateY(10rem);
  transition: 0.2s linear;
}
.blog .imagen .iconos a {
  font-size: 1.5rem;
  color: #FFFFFF;
}
.blog .imagen .iconos a i {
  padding-right: 0.5rem;
  color: #eacf4f;
}
.blog .imagen .iconos a:hover {
  color: #eacf4f;
}
.blog .imagen img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: 0.2s linear;
}
.blog .contenido {
  text-align: center;
  padding: 2rem;
}
.blog .contenido h3 {
  font-size: 2rem;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 0;
}
.blog .contenido p {
  font-size: 1.5rem;
  line-height: 2;
  color: #aaa;
}

.footer {
  text-align: center;
  background: linear-gradient(transparent, #000);
}
.footer .links a {
  margin: 1rem;
}
.footer .credit {
  font-size: 2rem;
  line-height: 2;
  color: #aaa;
}
.footer .credit span {
  color: #eacf4f;
}